import React, { useState } from 'react';
import { Container, Typography, Button, CircularProgress, Alert } from '@mui/material';
import { motion } from 'framer-motion';
import api from '../api';

export default function Checkout() {
  const [loading, setLoading] = useState(false);
  const [success, setSuccess] = useState(false);
  const [error, setError] = useState('');

  const handleCheckout = async () => {
    setLoading(true);
    setError('');
    setSuccess(false);
    try {
      // Place order
      await api.post('/orders');
      // Mock payment
      await api.post('/payment');
      setSuccess(true);
    } catch (err) {
      setError(err.response?.data?.message || 'Checkout failed');
    } finally {
      setLoading(false);
    }
  };

  return (
    <Container>
      <motion.div initial={{ opacity: 0 }} animate={{ opacity: 1 }}>
        <Typography variant="h4" gutterBottom>Checkout</Typography>
        {success ? (
          <Alert severity="success">Order placed and payment processed!</Alert>
        ) : (
          <Button variant="contained" color="primary" onClick={handleCheckout} disabled={loading}>
            {loading ? <CircularProgress size={24} /> : 'Place Order & Pay'}
          </Button>
        )}
        {error && <Alert severity="error" sx={{ mt: 2 }}>{error}</Alert>}
      </motion.div>
    </Container>
  );
} 