import React from 'react';
import { AppBar, Toolbar, Typography, Button, Box } from '@mui/material';
import { Link, useNavigate } from 'react-router-dom';
import { motion } from 'framer-motion';

export default function Navbar({ isAdmin, isLoggedIn, onLogout }) {
  const navigate = useNavigate();
  return (
    <motion.div initial={{ y: -40, opacity: 0 }} animate={{ y: 0, opacity: 1 }}>
      <AppBar position="static">
        <Toolbar>
          <Typography variant="h6" sx={{ flexGrow: 1, cursor: 'pointer' }} onClick={() => navigate('/')}>Grocery</Typography>
          <Box>
            <Button color="inherit" component={Link} to="/">Home</Button>
            <Button color="inherit" component={Link} to="/cart">Cart</Button>
            {isLoggedIn && <Button color="inherit" component={Link} to="/orders">Orders</Button>}
            {isAdmin && <Button color="inherit" component={Link} to="/admin">Admin</Button>}
            {!isLoggedIn ? (
              <>
                <Button color="inherit" component={Link} to="/login">Login</Button>
                <Button color="inherit" component={Link} to="/register">Register</Button>
              </>
            ) : (
              <Button color="inherit" onClick={onLogout}>Logout</Button>
            )}
          </Box>
        </Toolbar>
      </AppBar>
    </motion.div>
  );
} 