import React, { useEffect, useState } from 'react';
import { Container, Typography, List, ListItem, ListItemText, IconButton, Button } from '@mui/material';
import DeleteIcon from '@mui/icons-material/Delete';
import { motion } from 'framer-motion';
import api from '../api';

export default function Cart({ onCheckout }) {
  const [cart, setCart] = useState([]);

  const fetchCart = () => {
    api.get('/cart').then(res => setCart(res.data.cart));
  };

  useEffect(() => {
    fetchCart();
  }, []);

  const handleRemove = (productId) => {
    api.delete(`/cart/${productId}`).then(fetchCart);
  };

  const handleCheckout = () => {
    onCheckout && onCheckout();
  };

  return (
    <Container>
      <motion.div initial={{ opacity: 0 }} animate={{ opacity: 1 }}>
        <Typography variant="h4" gutterBottom>Cart</Typography>
        <List>
          {cart.map(item => (
            <ListItem key={item.productId} secondaryAction={
              <IconButton edge="end" onClick={() => handleRemove(item.productId)}>
                <DeleteIcon />
              </IconButton>
            }>
              <ListItemText primary={`Product ID: ${item.productId}`} secondary={`Quantity: ${item.quantity}`} />
            </ListItem>
          ))}
        </List>
        <Button variant="contained" color="primary" onClick={handleCheckout} disabled={cart.length === 0}>
          Proceed to Checkout
        </Button>
      </motion.div>
    </Container>
  );
} 