import React, { useState, useEffect, useCallback } from 'react';
import { BrowserRouter as Router, Routes, Route, useNavigate, Navigate } from 'react-router-dom';
import Home from './pages/Home';
import Login from './pages/Login';
import Register from './pages/Register';
import Cart from './pages/Cart';
import Checkout from './pages/Checkout';
import Orders from './pages/Orders';
import Admin from './pages/Admin';
import Navbar from './components/Navbar';
import api from './api';

function AppRoutes({ isLoggedIn, isAdmin, onLogin, onLogout }) {
  return (
    <>
      <Navbar isAdmin={isAdmin} isLoggedIn={isLoggedIn} onLogout={onLogout} />
      <Routes>
        <Route path="/" element={<Home />} />
        <Route path="/login" element={isLoggedIn ? <Navigate to="/" /> : <Login onLogin={onLogin} />} />
        <Route path="/register" element={isLoggedIn ? <Navigate to="/" /> : <Register onRegister={onLogin} />} />
        <Route path="/cart" element={isLoggedIn ? <Cart /> : <Navigate to="/login" />} />
        <Route path="/checkout" element={isLoggedIn ? <Checkout /> : <Navigate to="/login" />} />
        <Route path="/orders" element={isLoggedIn ? <Orders /> : <Navigate to="/login" />} />
        <Route path="/admin" element={isAdmin ? <Admin /> : <Navigate to="/" />} />
      </Routes>
    </>
  );
}

export default function App() {
  const [isLoggedIn, setIsLoggedIn] = useState(!!localStorage.getItem('token'));
  const [isAdmin, setIsAdmin] = useState(false);

  const checkAdmin = useCallback(async () => {
    if (!localStorage.getItem('token')) {
      setIsAdmin(false);
      return;
    }
    try {
      // Decode JWT to check admin (or call a /me endpoint if available)
      const token = localStorage.getItem('token');
      const payload = JSON.parse(atob(token.split('.')[1]));
      setIsAdmin(!!payload.is_admin);
    } catch {
      setIsAdmin(false);
    }
  }, []);

  useEffect(() => {
    checkAdmin();
  }, [isLoggedIn, checkAdmin]);

  const handleLogin = () => {
    setIsLoggedIn(true);
    checkAdmin();
  };

  const handleLogout = () => {
    localStorage.removeItem('token');
    setIsLoggedIn(false);
    setIsAdmin(false);
  };

  return (
    <Router>
      <AppRoutes isLoggedIn={isLoggedIn} isAdmin={isAdmin} onLogin={handleLogin} onLogout={handleLogout} />
    </Router>
  );
} 