import React, { useEffect, useState } from 'react';
import { Container, Grid, Typography, Snackbar, Alert, Box, Button, Chip, Stack, Card, CardContent, CardMedia } from '@mui/material';
import { motion } from 'framer-motion';
import api from '../api';
import ProductCard from '../components/ProductCard';

const HERO_BANNER = {
  title: 'Welcome to FreshMart!',
  subtitle: 'Your one-stop shop for fresh groceries, delivered fast.',
  image: 'https://images.unsplash.com/photo-1504674900247-0877df9cc836?auto=format&fit=crop&w=1200&q=80',
};

export default function Home() {
  const [products, setProducts] = useState([]);
  const [categories, setCategories] = useState([]);
  const [selectedCategory, setSelectedCategory] = useState('All');
  const [snackbar, setSnackbar] = useState({ open: false, message: '', severity: 'success' });

  useEffect(() => {
    api.get('/products').then(res => {
      setProducts(res.data);
      const cats = Array.from(new Set(res.data.map(p => p.category).filter(Boolean)));
      setCategories(cats);
    });
  }, []);

  const handleAddToCart = async (product) => {
    try {
      await api.post('/cart', { productId: product.id, quantity: 1 });
      setSnackbar({ open: true, message: 'Added to cart!', severity: 'success' });
    } catch (err) {
      setSnackbar({ open: true, message: 'Login required to add to cart.', severity: 'error' });
    }
  };

  // Featured: first 3 products
  const featured = products.slice(0, 3);
  // Weekly Deals: products with price < median
  const prices = products.map(p => p.price).sort((a, b) => a - b);
  const median = prices.length ? prices[Math.floor(prices.length / 2)] : 0;
  const weeklyDeals = products.filter(p => p.price <= median).slice(0, 3);
  // Popular Categories: top 3 by count
  const categoryCounts = categories.map(cat => ({
    name: cat,
    count: products.filter(p => p.category === cat).length,
  })).sort((a, b) => b.count - a.count).slice(0, 3);

  // Filtered products
  const filteredProducts = selectedCategory === 'All'
    ? products
    : products.filter(p => p.category === selectedCategory);

  return (
    <Container maxWidth="lg">
      {/* Hero Banner */}
      <motion.div initial={{ opacity: 0, y: -40 }} animate={{ opacity: 1, y: 0 }}>
        <Box sx={{
          background: `linear-gradient(90deg, rgba(0,0,0,0.5) 60%, transparent), url(${HERO_BANNER.image}) center/cover`,
          color: 'white',
          borderRadius: 3,
          p: 6,
          mb: 4,
          minHeight: 280,
          display: 'flex',
          flexDirection: 'column',
          justifyContent: 'center',
        }}>
          <Typography variant="h3" fontWeight={700} gutterBottom>{HERO_BANNER.title}</Typography>
          <Typography variant="h5" mb={2}>{HERO_BANNER.subtitle}</Typography>
          <Button variant="contained" color="secondary" size="large" sx={{ width: 200 }} href="#products">Shop Now</Button>
        </Box>
      </motion.div>

      {/* Category Filter */}
      <motion.div initial={{ opacity: 0 }} animate={{ opacity: 1 }}>
        <Stack direction="row" spacing={2} mb={4} alignItems="center">
          <Typography variant="h6">Categories:</Typography>
          <Chip
            label="All"
            color={selectedCategory === 'All' ? 'primary' : 'default'}
            onClick={() => setSelectedCategory('All')}
          />
          {categories.map(cat => (
            <Chip
              key={cat}
              label={cat}
              color={selectedCategory === cat ? 'primary' : 'default'}
              onClick={() => setSelectedCategory(cat)}
            />
          ))}
        </Stack>
      </motion.div>

      {/* Featured Products */}
      <motion.div initial={{ opacity: 0 }} animate={{ opacity: 1 }}>
        <Typography variant="h5" fontWeight={600} mb={2}>Featured Products</Typography>
        <Grid container spacing={2} mb={4}>
          {featured.map(product => (
            <Grid item xs={12} sm={6} md={4} key={product.id}>
              <ProductCard product={product} onAddToCart={handleAddToCart} />
            </Grid>
          ))}
        </Grid>
      </motion.div>

      {/* Popular Categories */}
      {categoryCounts.length > 0 && (
        <motion.div initial={{ opacity: 0 }} animate={{ opacity: 1 }}>
          <Typography variant="h5" fontWeight={600} mb={2}>Popular Categories</Typography>
          <Grid container spacing={2} mb={4}>
            {categoryCounts.map(cat => (
              <Grid item xs={12} sm={4} key={cat.name}>
                <Card sx={{ cursor: 'pointer' }} onClick={() => setSelectedCategory(cat.name)}>
                  <CardContent>
                    <Typography variant="h6">{cat.name}</Typography>
                    <Typography variant="body2" color="text.secondary">{cat.count} products</Typography>
                  </CardContent>
                </Card>
              </Grid>
            ))}
          </Grid>
        </motion.div>
      )}

      {/* Weekly Deals */}
      {weeklyDeals.length > 0 && (
        <motion.div initial={{ opacity: 0 }} animate={{ opacity: 1 }}>
          <Typography variant="h5" fontWeight={600} mb={2}>Weekly Deals</Typography>
          <Grid container spacing={2} mb={4}>
            {weeklyDeals.map(product => (
              <Grid item xs={12} sm={6} md={4} key={product.id}>
                <ProductCard product={product} onAddToCart={handleAddToCart} />
              </Grid>
            ))}
          </Grid>
        </motion.div>
      )}

      {/* All Products */}
      <motion.div initial={{ opacity: 0 }} animate={{ opacity: 1 }}>
        <Typography variant="h5" fontWeight={600} mb={2} id="products">{selectedCategory === 'All' ? 'All Products' : `${selectedCategory} Products`}</Typography>
        <Grid container spacing={2}>
          {filteredProducts.map(product => (
            <Grid item xs={12} sm={6} md={4} key={product.id}>
              <ProductCard product={product} onAddToCart={handleAddToCart} />
            </Grid>
          ))}
        </Grid>
      </motion.div>

      <Snackbar open={snackbar.open} autoHideDuration={2000} onClose={() => setSnackbar({ ...snackbar, open: false })}>
        <Alert severity={snackbar.severity} sx={{ width: '100%' }}>{snackbar.message}</Alert>
      </Snackbar>
    </Container>
  );
} 