import React, { useEffect, useState } from 'react';
import { Container, Typography, List, ListItem, ListItemText, Divider, Alert, Button, Dialog, DialogTitle, DialogContent, DialogActions, TextField, IconButton } from '@mui/material';
import { motion } from 'framer-motion';
import api from '../api';
import DeleteIcon from '@mui/icons-material/Delete';
import EditIcon from '@mui/icons-material/Edit';

export default function Admin() {
  const [orders, setOrders] = useState([]);
  const [products, setProducts] = useState([]);
  const [error, setError] = useState('');
  const [openDialog, setOpenDialog] = useState(false);
  const [editProduct, setEditProduct] = useState(null);
  const [form, setForm] = useState({ name: '', description: '', price: '', image: '', category: '' });

  // Fetch orders and products
  useEffect(() => {
    api.get('/admin/orders')
      .then(res => setOrders(res.data))
      .catch(err => setError('Not authorized or error fetching orders.'));
    api.get('/products').then(res => setProducts(res.data));
  }, []);

  // Handle form changes
  const handleFormChange = e => setForm({ ...form, [e.target.name]: e.target.value });

  // Open dialog for add/edit
  const handleOpenDialog = (product = null) => {
    setEditProduct(product);
    setForm(product ? { ...product } : { name: '', description: '', price: '', image: '', category: '' });
    setOpenDialog(true);
  };
  const handleCloseDialog = () => setOpenDialog(false);

  // Add or update product
  const handleSaveProduct = async () => {
    try {
      if (editProduct) {
        await api.put(`/products/${editProduct.id}`, form);
      } else {
        await api.post('/products', form);
      }
      const res = await api.get('/products');
      setProducts(res.data);
      setOpenDialog(false);
    } catch (err) {
      setError('Error saving product.');
    }
  };

  // Delete product
  const handleDeleteProduct = async (id) => {
    try {
      await api.delete(`/products/${id}`);
      setProducts(products.filter(p => p.id !== id));
    } catch (err) {
      setError('Error deleting product.');
    }
  };

  return (
    <Container>
      <motion.div initial={{ opacity: 0 }} animate={{ opacity: 1 }}>
        <Typography variant="h4" gutterBottom>Admin - All Orders</Typography>
        {error && <Alert severity="error">{error}</Alert>}
        <List>
          {orders.map(order => (
            <React.Fragment key={order.id}>
              <ListItem>
                <ListItemText
                  primary={`Order #${order.id} - $${order.total} (User: ${order.user_id})`}
                  secondary={
                    <>
                      {order.items.map(item => (
                        <div key={item.productId}>
                          Product ID: {item.productId}, Quantity: {item.quantity}, Price: ${item.price}
                        </div>
                      ))}
                      <div>Date: {new Date(order.created_at).toLocaleString()}</div>
                    </>
                  }
                />
              </ListItem>
              <Divider />
            </React.Fragment>
          ))}
        </List>
        <Typography variant="h4" gutterBottom sx={{ mt: 4 }}>Product Management</Typography>
        <Button variant="contained" color="primary" onClick={() => handleOpenDialog()}>Add Product</Button>
        <List>
          {products.map(product => (
            <React.Fragment key={product.id}>
              <ListItem
                secondaryAction={
                  <>
                    <IconButton edge="end" onClick={() => handleOpenDialog(product)}><EditIcon /></IconButton>
                    <IconButton edge="end" onClick={() => handleDeleteProduct(product.id)}><DeleteIcon /></IconButton>
                  </>
                }
              >
                <ListItemText
                  primary={`${product.name} - $${product.price}`}
                  secondary={product.description}
                />
              </ListItem>
              <Divider />
            </React.Fragment>
          ))}
        </List>
        <Dialog open={openDialog} onClose={handleCloseDialog}>
          <DialogTitle>{editProduct ? 'Edit Product' : 'Add Product'}</DialogTitle>
          <DialogContent>
            <TextField margin="dense" label="Name" name="name" fullWidth value={form.name} onChange={handleFormChange} />
            <TextField margin="dense" label="Description" name="description" fullWidth value={form.description} onChange={handleFormChange} />
            <TextField margin="dense" label="Price" name="price" type="number" fullWidth value={form.price} onChange={handleFormChange} />
            <TextField margin="dense" label="Image URL" name="image" fullWidth value={form.image} onChange={handleFormChange} />
            <TextField margin="dense" label="Category" name="category" fullWidth value={form.category} onChange={handleFormChange} />
          </DialogContent>
          <DialogActions>
            <Button onClick={handleCloseDialog}>Cancel</Button>
            <Button onClick={handleSaveProduct} variant="contained">Save</Button>
          </DialogActions>
        </Dialog>
      </motion.div>
    </Container>
  );
} 